  background: #eaeaea;
  margin-top: 30px;
  /* Задание новых стилей слайдера */
  /****************************************** Планшетный режим ****************************/
  /****************************************** Десктопный режим ****************************/
}
.slider .slider__image--before {
  margin-left: -15px;
}
.slider .slider__image--after {
  display: none;
}
.slider .slider__controls-slider {
  -webkit-appearance: none;
  width: 84px;
  height: 14px;
  background: #FFFFFF;
  border: 1px solid #CDCDCD;
  box-sizing: border-box;
  border-radius: 6px;
  padding: 0 3px;
}
.slider .slider__controls-slider::-webkit-slider-thumb {
  appearance: none;
  width: 35px;
  height: 6px;
  background: #68B738;
  border-radius: 6px;
  cursor: pointer;
}
.slider .slider__controls {
  margin-top: 20px;
  text-align: center;
}
.slider .slider__controls-text {
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .slider {
    /* Задание новых стилей слайдера */
  }
  .slider .slider__image--before {
    display: block;
    width: 606px;
    height: 476px;
    margin: 0 auto;
  }
  .slider .slider__controls-slider {
    width: 428px;
    height: 6px;
    background: #DCDCDC;
    border-radius: 6px;
    padding: 0 3px;
  }
  .slider .slider__controls-slider::-webkit-slider-thumb {
    appearance: none;
    width: 34px;
    height: 34px;
    background: #FFFFFF;
    border: 1px solid #CDCDCD;
    box-sizing: border-box;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
  }
  .slider .new_slider__controls-slider {
    display: block;
    position: absolute;
    width: 8px;
    height: 8px;
    background: #68B738;
    border-radius: 50%;
  }
  .slider .slider__controls {
    width: 580px;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    margin-top: 10px;
  }
  .slider .slider__controls-text {
    font-family: "Oswald-Regular", "Arial", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: normal;
    line-height: 30px;
    display: flex;
    padding: 0px 20px;
  }
  .slider .slider__controls-slider {
    width: 426px;
  }
}
@media (min-width: 1440px) {
  .slider .slider__image--before {
    width: 680px;
    height: 530px;
  }
}
@media (min-width: 768px) {
  .slider {
    background: none;
    margin-top: 80px;
    margin-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .slider {
    position: relative;
    margin-top: -355px;
    margin-left: 605px;
  }
}
